const Widgets = React.lazy(() => import('./views/widgets/Widgets'));
const Users = React.lazy(() => import('./views/users/Users'));
const User = React.lazy(() => import('./views/users/User'));

//MASTER
const Branch = React.lazy(() => import('./views/master/branch/index'));
const Company = React.lazy(() => import('./views/master/company/index'));
const Employee = React.lazy(() => import('./views/master/employee/index'));
const Item = React.lazy(() => import('./views/master/item/index'));
const Price = React.lazy(() => import('./views/master/price/index'));
const Period = React.lazy(() => import('./views/master/period/index'));
const SrcDocNo = React.lazy(() => import('./views/master/srcdocno/index'));

//PROMO
const Promo = React.lazy(() => import('./views/promo/index'));

const BranchCreate = React.lazy(() => import('./views/master/branch/_form'));
const CompanyCreate = React.lazy(() => import('./views/master/company/_form'));
const EmployeeCreate = React.lazy(() => import('./views/master/employee/_form'));
const PeriodCreate = React.lazy(() => import('./views/master/period/_form'));
const ItemCreate = React.lazy(() => import('./views/master/item/_form'));
const ItemPriceCreate = React.lazy(() => import('./views/master/price/_form'));
const DocNoCreate = React.lazy(() => import('./views/master/srcdocno/_form'));

//PROMO
const PromoCreate = React.lazy(() => import('./views/promo/_form'));

//FavItem & StockOpname Item
const ItemFav = React.lazy(() => import('./views/master/item/favorite'));
const ItemStock = React.lazy(() => import('./views/master/item/stock_opname'));


const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },
  
  
  //MASTER
  { path: '/branch', name: 'Branch', component: Branch, exact: true },
  { path: '/company', name: 'Company', component: Company, exact: true },
  { path: '/employee', name: 'Employee', component: Employee, exact: true },
  { path: '/item', name: 'Item', component: Item, exact: true },
  { path: '/price', name: 'Price', component: Price, exact: true },
  { path: '/period', name: 'Period', component: Period, exact: true },
  { path: '/docno', name: 'SrcDocNo', component: SrcDocNo, exact: true },

  //PROMO
  { path: '/promo', name: 'Promo', component: Promo, exact: true },

  //MASTER CREATED
  { path: '/branch-create', name: 'Branch', component: BranchCreate, exact: true },
  { path: '/company-create', name: 'Company', component: CompanyCreate, exact: true },
  { path: '/employee-create', name: 'Employee', component: EmployeeCreate, exact: true },
  { path: '/period-create', name: 'Period', component: PeriodCreate, exact: true },
  { path: '/item-create', name: 'Item', component: ItemCreate, exact: true },
  { path: '/price-create', name: 'Price', component: ItemPriceCreate, exact: true },
  { path: '/srcdoc-create', name: 'DocNo', component: DocNoCreate, exact: true },
  { path: '/promo-create', name: 'Promo', component: PromoCreate, exact: true },
  // { path: '/price-create', name: 'Price', component: Price, exact: true },
  // { path: '/period-create', name: 'Period', component: Period, exact: true },
  // { path: '/docno-create', name: 'SrcDocNo', component: SrcDocNo, exact: true },

  //ItemFav & Item StockOpname
  { path: '/item-favorite', name: 'Item Favorite', component: ItemFav, exact: true },
  { path: '/item-opname', name: 'Stock Opname', component: ItemStock, exact: true },


  { path: '/theme', name: 'Theme', component: Colors, exact: true },
  { path: '/theme/colors', name: 'Colors', component: Colors },
  { path: '/theme/typography', name: 'Typography', component: Typography },
  { path: '/base', name: 'Base', component: Cards, exact: true },
  { path: '/base/breadcrumbs', name: 'Breadcrumbs', component: Breadcrumbs },
  { path: '/base/cards', name: 'Cards', component: Cards },
  { path: '/base/carousels', name: 'Carousel', component: Carousels },
  { path: '/base/collapses', name: 'Collapse', component: Collapses },
  { path: '/base/forms', name: 'Forms', component: BasicForms },
  { path: '/base/jumbotrons', name: 'Jumbotrons', component: Jumbotrons },
  { path: '/base/list-groups', name: 'List Groups', component: ListGroups },
  { path: '/base/navbars', name: 'Navbars', component: Navbars },
  { path: '/base/navs', name: 'Navs', component: Navs },
  { path: '/base/paginations', name: 'Paginations', component: Paginations },
  { path: '/base/popovers', name: 'Popovers', component: Popovers },
  { path: '/base/progress-bar', name: 'Progress Bar', component: ProgressBar },
  { path: '/base/switches', name: 'Switches', component: Switches },
  { path: '/base/tables', name: 'Tables', component: Tables },
  { path: '/base/tabs', name: 'Tabs', component: Tabs },
  { path: '/base/tooltips', name: 'Tooltips', component: Tooltips },
  { path: '/buttons', name: 'Buttons', component: Buttons, exact: true },
  { path: '/buttons/buttons', name: 'Buttons', component: Buttons },
  { path: '/buttons/button-dropdowns', name: 'Dropdowns', component: ButtonDropdowns },
  { path: '/buttons/button-groups', name: 'Button Groups', component: ButtonGroups },
  { path: '/buttons/brand-buttons', name: 'Brand Buttons', component: BrandButtons },
  { path: '/charts', name: 'Charts', component: Charts },
  { path: '/icons', exact: true, name: 'Icons', component: CoreUIIcons },
  { path: '/icons/coreui-icons', name: 'CoreUI Icons', component: CoreUIIcons },
  { path: '/icons/flags', name: 'Flags', component: Flags },
  { path: '/icons/brands', name: 'Brands', component: Brands },
  { path: '/notifications', name: 'Notifications', component: Alerts, exact: true },
  { path: '/notifications/alerts', name: 'Alerts', component: Alerts },
  { path: '/notifications/badges', name: 'Badges', component: Badges },
  { path: '/notifications/modals', name: 'Modals', component: Modals },
  { path: '/notifications/toaster', name: 'Toaster', component: Toaster },
  { path: '/widgets', name: 'Widgets', component: Widgets },
  { path: '/users', exact: true,  name: 'Users', component: Users },
  { path: '/users/:id', exact: true, name: 'User Details', component: User }
];

export default routes;
